.logo {
    animation: App-logo-spin infinite 20s linear;
    height: 5vmin;
    pointer-events: none;
}

.header {
    background-color: #282c34;
    height: 5vh;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    font-size: calc(10px + 1.5vmin);
    color: white;
    cursor: default;
}

.header_link {
    color: #61dafb;
    padding-left: 1rem;
    text-decoration: none;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}
/*
#576574
*/
.Wrapper {
    position: fixed;
    width: 100%;
    height: 90%;
    overflow: auto;
    display: flex;
    flex-flow: row;
    flex-wrap: wrap;
    justify-content: center;
    background: #2c3e50;
    background: -webkit-linear-gradient(to right, #4ca1af, #2c3e50);
    background: linear-gradient(to right, #4ca1af, #2c3e50);
}

.Editor_textarea,
.PreviewBox {
    margin: 2rem;
    width: 40vw;
    height: 75vh;
    font-size: 1rem;
    resize: none;
    border: 1px solid black;
    outline: none;
    padding: 1rem;
    overflow: auto;
    background-color: #f1f2f6;
    border-radius: 5px;
    -webkit-box-shadow: 2px 2px 4px 1px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 2px 2px 4px 1px rgba(0, 0, 0, 0.75);
    box-shadow: 2px 2px 4px 1px rgba(0, 0, 0, 0.75);
}

.Output {
    margin: 0 auto;
}

.Output img {
    max-width: 50%;
    height: auto;
}

@media (max-width: 1100px) {
    .Editor_textarea,
    .PreviewBox {
        width: 75vw;
        height: 35vh;
        margin: 1rem;
    }
    .header {
        font-size: 3vmin;
    }
}

.Footer {
    position: fixed;
    bottom: 0;
    left: 0;
    cursor: default;
    background-color: #282c34;
    height: 5vh;
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    font-size: calc(5px + 1vmin);
    color: white;
}
